import React from 'react';
import { FC, useEffect, useState } from 'react';
import { Button, Input, Space, InputNumber, Form, message } from 'antd';
import { InfoCircleOutlined } from '@ant-design/icons';
import './RatioCalculateTool.less';


const recognitionValueInfo = "',''*''x',6";

const copyValueInfo = "";

const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
};

const RatioCalculateTool: FC = () => {
    const [awaitConvertValueList, setAwaitConvertValueList] = useState([0, 0, 0]); // 
    const [convertValueList, setConvertValueList] = useState([0, 0, 0]); // 
    const [conversionRateValue, setConversionRateValue] = useState<number>(0.5); // 
    const [recognitionValue, setRecognitionValue] = useState<string>(''); //  
    const [copyValue, setCopyValue] = useState<any>(''); //  
    const [messageApi, contextHolder] = message.useMessage();

    const successClue = () => {
        messageApi.open({
            type: 'success',
            content: '',
        });
    };

    const [form_left] = Form.useForm();
    const [form_right] = Form.useForm();

    const onChangeConversionRate = (value) => {
        console.log('onChangeConversionRate--->', value);
        setConversionRateValue(value)
    }

    const onFinishLeftForm = (value: number[]) => {
        console.log('onFinishLeftForm--->', value);
        let tempArr: number[] = [];
        tempArr = Object.values(value).map((item: number) => {
            return item * conversionRateValue
        })
        setConvertValueList(tempArr);
        areSetForm(form_right, tempArr)
    }

    const onFinishRightForm = (value) => {
        console.log('onFinishRightForm--->', value);
    }

    /**
     * form
     * @formInstance form
     * @list 
     * 
     */
    const areSetForm = (formInstance, list: number[]) => {
        let tempObj = {}
        if (formInstance && formInstance.setFieldsValue) {
            list.map((item, index) => {
                tempObj[`value_${index + 1}`] = item;
            })
            formInstance.setFieldsValue({ ...tempObj });
        }
    }

    /**
     * 
     */
    const convert = () => {
        form_left.submit();
    }

    /**
     * 
     */
    const oneKeyConvert = () => {
        recognitionButton();
        form_left.submit();
    }

    /**
     *  
     */
    const recognitionButton = () => {
        // console.log('recognitionValue--->', recognitionValue);
        let tempArr: number[] = [], tempValue = 0;
        // 
        for (let i = 0; i < recognitionValue.length; i++) {
            console.log(recognitionValue[i]);
            if (recognitionValue[i] === ',' || recognitionValue[i] === '' || recognitionValue[i] === 'x' || recognitionValue[i] === '*') {
                tempArr.push(tempValue);
                tempValue = 0;
                continue;
            } else {
                tempValue = tempValue * 10 + Number(recognitionValue[i])
            }
        }
        tempArr.push(tempValue);
        setAwaitConvertValueList(tempArr)
    }

    /**
     * 
     */
    const copyButton = () => {
        copy(copyValue);
    }

    /**
     * 
     */
    const copy = (text) => {
        // text
        // input
        const el = document.createElement('input')
        // input
        el.setAttribute('value', text)
        // input
        document.body.appendChild(el)
        // input
        el.select()
        // 
        document.execCommand('copy')
        // input
        document.body.removeChild(el)
        successClue();
    }

    useEffect(() => {
        areSetForm(form_left, awaitConvertValueList);
    }, [awaitConvertValueList])

    useEffect(() => {
        setCopyValue(convertValueList)
    }, [convertValueList])

    return (
        <div className="RatioCalculateTool-mainContainer">
            {contextHolder}
            <div className="github-icon" title='github' onClick={() => { window.open('https://github.com/front-end-study-GoGoGo/online-ratio-conversion') }}>
                <svg height="32" aria-hidden="true" viewBox="0 0 16 16" version="1.1" width="32" data-view-component="true" className="octicon octicon-mark-github v-align-middle">
                    <path fill-rule="evenodd" d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0016 8c0-4.42-3.58-8-8-8z"></path>
                </svg>
            </div>
            <div>
                <Space>
                    <Button onClick={() => { recognitionButton() }}></Button>
                    <Input
                        style={{ width: '300px' }}
                        value={recognitionValue}
                        onChange={(e) => setRecognitionValue(e.target.value)}
                        placeholder={`',''*''x'`}
                        onPressEnter={(e: any) => {
                            // console.log('111', e.target.value)
                            setRecognitionValue(e.target.value)
                            oneKeyConvert();
                        }}
                    ></Input>
                    <InfoCircleOutlined title={recognitionValueInfo} />
                </Space>
            </div>
            <div>
                <Space>
                    <Button onClick={() => { copyButton() }}></Button>
                    <Input style={{ width: '300px' }} value={copyValue} onChange={(e) => setCopyValue(e.target.value)}></Input>
                    <InfoCircleOutlined title={copyValueInfo} />
                </Space>
            </div>
            <div className="RatioCalculateTool-contentBox">
                <div className="RatioCalculateTool-left">
                    <div></div>
                    <Form {...layout} form={form_left} name="control-hooks" onFinish={onFinishLeftForm}>
                        {awaitConvertValueList.map((item, index) => {
                            return <Form.Item key={index} name={`value_${index + 1}`} label={`_${index + 1}`}>
                                <Input />
                            </Form.Item>
                        })}
                    </Form>
                </div>
                <div className="RatioCalculateTool-center">
                    <Space direction="vertical" size={20}>
                        <div>
                            :&nbsp;&nbsp;<InputNumber min={-100} max={1000} value={conversionRateValue} defaultValue={3} onChange={onChangeConversionRate}></InputNumber>
                        </div>
                        <Button onClick={() => { convert() }}></Button>
                        <Button onClick={() => { oneKeyConvert() }}></Button>
                        {/* <Button onClick={() => {}}></Button> */}
                        {/* <Button></Button>
                        <Button></Button> */}
                    </Space>
                </div>
                <div className="RatioCalculateTool-right">
                    <div></div>
                    <Form {...layout} form={form_right} name="control-hooks" onFinish={onFinishRightForm}>
                        {awaitConvertValueList.map((item, index) => {
                            return <Form.Item key={index} name={`value_${index + 1}`} label={`_${index + 1}`}>
                                <Input />
                            </Form.Item>
                        })}
                    </Form>
                </div>
            </div>
        </div>
    );
}

export default RatioCalculateTool;
